import { useState } from "react";
import ShowSVG from "@/components/ShowSVG";
import Briefcase from "@/components/icons/briefcase.svg";

import SocialLinks from "@/components/SocialLinks";

export default function Header() {
    return (
        <header className="bg-opacity-50 backdrop-blur-sm bg-neutral-primary fixed w-full z-20 top-0 start-0 border-b border-default">
            <div className="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4">
                <div>
                    <ShowSVG svg={Briefcase} />
                </div>

                <div className="flex items-center justify-center gap-4">
                    <a href="#">Home</a>
                    <a href="#">About</a>
                    <a href="#">Projects</a>
                    <a href="#">Contact</a>
                </div>

                <div>
                    <SocialLinks />
                </div>
            </div>
        </header>
    );
}